<template>
	<transition name="fade">
	<div id="articleshow" v-if="artRouterActive">
		<div class="current">
			<span>当前位置：</span><router-link :to="{path: '/'}">首页</router-link>><router-link :to="{path: '/articles'}">项目库</router-link>>
			<span>{{ articleShow.name }}</span>
		</div>
		<div class="news_cont">
			<h1>{{ articleShow.name }}</h1>
			<div class="auther clearfix">
				<span>作者：美味学院</span>
				<span>发布时间：{{ articleShow.created_time_date }}</span>
				<span>浏览量：{{ articleShow.view_count }}</span>
			</div>
			<div class="news_text" v-html="articleShow.body"></div>
			<div class="through_page">
				<p>
					<span>上一篇：</span>
					<router-link :to="{ path: '/articles/' + articleNextPrev.prev.id }">{{ articleNextPrev.prev.name }}</router-link>
				</p>
				<p>
					<span>下一篇：</span>
					<router-link :to="{ path: '/articles/' + articleNextPrev.next.id }">{{ articleNextPrev.next.name }}</router-link>
				</p>
			</div>
		</div>
		<Hot />
	</div>
	</transition>
</template>

<script>
import Hot from '../soncomponents/hotrecommend.vue';
export default {
	name: 'articleShow',
	data() {
		return {
			articleShow: [],
			articleNextPrev: {
				next: {},
				prev: {}
			},
			artRouterActive: true
		};
	},
	components:{
		Hot
	},
	created() {
		var id = this.$route.params.id;
		this.getarticleId(id);
		console.log(id);
	},
	methods: {
		getarticleId(id) {
			this.$axios
				.get(this.HOST + 'articles/' + id, {
					headers: {
						Accept: 'application/prs.meiweixueyuan.v1+json'
					}
				})
				.then(res => {
					this.articleShow = res.data;
				})
				.catch( error =>{
					console.log(error)
				});
			this.$axios.get( this.HOST + 'articleNextAndPrev/' + id,{
				headers: {
					Accept: 'application/prs.meiweixueyuan.v1+json'
				}
			})
			.then( res => {
				this.articleNextPrev = res.data;
				console.log(this.articleNextPrev)
			})
			.catch( error =>{
				console.log(error)
			})
		}
	},
	watch:{
		'$route'(to,from){//监听路由发生变化时，组件重新渲染，页面刷新
		　　this.artRouterActive = false;
			window.location.reload()
		}
	}
};
</script>

<style></style>



