<template>
<div class="form_submit">
    <div class="form_top">
        <p>全国连锁  特色培训</p>
        <div class="top_pw clearfix">
            <div class="form_top_pic"><img :src="require('../assets/images/from_top.png')" /></div>
            <div class="form_top_word">
                <p>专注特色美食短期培训<br />
                    核心技术、秘制配方<br />
                    创就业帮扶指导 助您快速成功成才</p>
            </div>
        </div>
    </div>
    <div class="form_input">
        <ul>
            <li class="username clearfix">
                <i></i>
                <input v-model="name" type="text" name="name" placeholder="请输入您的称呼" />
            </li>
            <li class="select clearfix">
                <i></i>
                <select v-model="select" id="select"  name="body">
                    <option value="">请选择你感兴趣的项目</option>
                    <option value="特色风味小吃培训">特色风味小吃培训</option>
                    <option value="面包 蛋糕 甜品热门培训">面包 蛋糕 甜品热门培训</option>
                    <option value="中式面点培训">中式面点培训</option>
                    <option value="新派中餐培训">新派中餐培训</option>
                    <option value="咖啡饮品培训">咖啡饮品培训</option>
                    <option value="西点西餐培训">西点西餐培训</option>
                </select>
                <span></span>
            </li>
            <li class="phone clearfix">
                <i></i>
                <input v-model="phone" type="number" name="mobile" placeholder="请输入您的电话号码" />
            </li>
            <li class="lock">
                <div class="lock_state">
                    <i></i>
                    <p>美味学院尊重并保护您的隐私</p>
                </div>
            </li>
            <li class="sub_btn">
                <input @click="submit($event)" type="submit" name="name" value="提交申请" />
            </li>
        </ul>
    </div>
</div>
</template>
<script>
export default{
    name: 'tables',
    data(){
        return{
                arror: [],
                name: '',
                select: '',
                phone: ''
        }
    },
    methods:{
        submit($event){
			var reg = /^1[3|4|5|6|7|8|9][0-9]\d{8}$/;
            this.error = [];
            
            if(this.name == ''){
                window.alert('你还没有输入你的称呼');
                return
            };
            if(this.select == ''){
                window.alert('请选择你感兴趣的项目');
                return
            };
            if(!reg.test(this.phone)){
                window.alert('电话号码不正确');
                return
            };
            if(this.name && this.select && this.phone){
				event.preventDefault();
				this.$axios.get('url',{
					params:{
						name: this.name,
						category_id: this.select,
						phone: this.phone
					}
				}).then((res) =>{
					console.log(res.data);
					
				}).catch(error =>{
					console.log(error)
				})
			};

           
        }
    }
}

</script>
<style>

</style>



