<template>
	<transition name="fade">
	<div id="questionshow" v-if="questionAlive">
		<div class="current">
			<span>当前位置：</span><router-link :to="{path: '/'}">首页</router-link>><router-link :to="{path: '/questions'}">创业答疑</router-link>>
			<span>{{ questionShow.question }}</span>
		</div>
		<div class="question_matter">
			<div class="question_tit">
				<h2><i></i>{{ questionShow.question }}</h2>
			</div>
			<div class="matter_q">
				<div class="matter_q_tit"><p>{{ questionShow.question }}</p></div>
				<div class="matter_q_time">
					<p>{{ questionShow.created_at }}<span>{{ questionShow.view_count }}</span>次浏览</p>
				</div>
			</div>
			<div class="matter_line"></div>
			<div class="answer_tit"><h3>全部答案</h3></div>
			<div class="answer_wrap">
				<template v-for="(ans,index) in answerList">
					<div class="answer_item" :key="index">
						<div class="answer_order"><p>答案{{ index+1 }}</p></div>
						<div class="answer_text">
							<p>{{ ans.answer }}</p>
						</div>
					</div>
				</template>
			</div>
			<div class="matter_line"></div>
			<div class="correlation">
				<div class="answer_tit"><h3>相关回答</h3></div>
				<div class="correlation_q">
					<ul>
						<template  v-for="querem in questionRecommend">
							<li>
								<span>·</span>
								<router-link :to="{ path: '/questions/' + querem.id}">{{ querem.question }}</router-link>
							</li>
						</template>
					</ul>
				</div>
				<div class="answer_page">
					<router-link v-if="prevshow" :to="{ path: '/questions/' + prev.id }" class="pre_page"><span>上一篇：</span>{{ prev.question }}</router-link>
					<router-link v-if="nextshow" :to="{ path: '/questions/' + next.id }"><span>下一篇：</span>{{ next.question }}</router-link>
				</div>
			</div>
			<div class="matter_line"></div>
			<div class="new_product">
				<div class="answer_tit noborder_a"><h3>新品上线</h3></div>
				<ul>
					<li v-for="querec in queRecommend">
						<router-link :to="{ path: '/courses/'+ querec.id+'/'+querec.slug}">
							<div class="new_product_pic"><img :src="querec.qiniu_url" /></div>
							<p>{{querec.name}}</p>
							<span>关注度：{{ querec.apply_count }}</span>
						</router-link>
					</li>
				</ul>
			</div>
		</div>
	</div>
	</transition>
</template>

<script>
export default {
	name: 'questionShow',
	data() {
		return {
			questionShow: [],
			answerList: [],
			questionRecommend: [],
			questionNextAndPrev: [],
			prev: {},
			next: {},
			queRecommend: [],
			prevshow: true,
			nextshow: true,
			questionAlive: true
			
		}
	},
	created() {
		var id = this.$route.params.id;
		this.getQuestion(id);
		//相关回答
		this.$axios.get( this.HOST + 'question/recommends', {
				headers:{
					Accept: 'application/prs.meiweixueyuan.v1+json'
				}
		})
		.then( res => {
			this.questionRecommend = res.data.data
			//console.log(this.questionRecommend)
		})
		.catch(error => {
			console.log(error);
		});
	},
	methods:{
		getQuestion(id){
			//问答详情
			this.$axios.get( this.HOST + '/questions/' + id +'?include=answers' ,{
			 headers:{
				 Accept: 'application/prs.meiweixueyuan.v1+json'
			 }	
			})
			.then( res => {
				this.questionShow = res.data;
				this.answerList = res.data.answers.data
			})
			.catch(error => {
				console.log(error);
			});
			//上下篇
			this.$axios.get( this.HOST + '/questionNextAndPrev/' + id ,{
			 headers:{
				 Accept: 'application/prs.meiweixueyuan.v1+json'
			 }	
			})
			.then( res => {
				this.prev = res.data.prev;
				if(this.prev === null){
					this.prev = '没有上一篇了，往下翻吧，说不定还挺多'
					this.prevshow = false;
					console.log(this.prev)
				};
				this.next = res.data.next;
				if(this.next == null){
					this.next = '没有下一篇了，等几天吧';
					this.nextshow = false;
					console.log(this.next)
				};
			})
			.catch(error => {
				console.log(error);
			});
			//新品上线
			this.$axios.get( this.HOST + '/recommendCourses',{
				headers:{
					Accept: 'application/prs.meiweixueyuan.v1+json'
				}
			})
			.then( res => {
				this.queRecommend = res.data.data
			})
			.catch( error =>{
				console.log(error)
			})
		},
	},
	watch:{
		'$route'(to,from){ //监听路由发生变化时，组件重新渲染，页面刷新
		　　this.isRouterActive = false;
		　　this.$nextTick(()=>(this.isRouterActive = true));
			window.location.reload()
		}
	}
};
</script>

<style></style>



