<template>
	<div id="questions">
		<div class="request_loading" v-if="loading">
			<div class="this_loading"></div>
		</div>
		<div class="current">
			<span>当前位置：</span><router-link :to="{path: '/'}">首页</router-link>><router-link :to="{path: 'questions'}">创业答疑</router-link>
		</div>
		<div class="qa_class">
			<div class="class_extend class_extend_pull">
				<router-link class="all_a" :to="{path: 'questions'}">全部<br />分类</router-link>
				<template v-for="(qaclass,id) in qaclassList">
					<router-link :to="{path: 'questions',query:{category_id: qaclass.id}}" :key="id">{{ qaclass.name }}</router-link>
				</template>
			</div>
			<div class="more_class">
				<p>点击展开全部分类</p>
				<span>^</span>
			</div>
		</div>
		<div class="qa_list">
			<div class="qa_list_title">
				<div class="qlt_wrap clearfix">
					<i></i>
					<h2>问答列表</h2>
				</div>
			</div>
			<template v-for="qa in qaList" @show="list">
				<router-link :to="{path: 'questions/'+qa.id}">
					<div class="qa_list_item clearfix">
						<div class="qli_wrap">
							<div class="question_q clearfix">
								<span>Q</span>
								<p>{{ qa.question }}</p>
							</div>
							<div class="answer_a clearfix">
								<span>A</span>
								<p>{{ qa.repliedAnswer.answer }}</p>
							</div>
						</div>
						<div class="qa_right_number clearfix">
							<p>{{ qa.answer_count }}</p>
							<p>个答案</p>
						</div>
					</div>
				</router-link>
			</template>
			
		</div>
	</div>
</template>

<script>
export default {
	name: 'questions',
	data() {
		return {
			queList: [],
			height: true,
			qaclassList: [],
			qaList: [],
			loading: false
		};
	},
	methods:{
		getQueData(){
			this.loading = true;
			//问答列表
			var queCateId = '&category_id=' + this.$route.query.category_id;
			if(!this.$route.query.category_id){
				queCateId = '';
			}
			this.$axios.get(this.HOST + 'questions?include=repliedAnswer,questionCategory' + queCateId,{
				headers: {
					 Accept: "application/prs.meiweixueyuan.v1+json"
				}
			}).then(res => {
				this.loading = false;
				this.qaList = res.data.data;
				return this.qaList.reverse();
				//console.log(this.qaList)
			}).catch(error => {
				console.log(error)
			});
		}
		
	},
	created() {
		//分类列表
		this.$axios
			.get(this.HOST + 'questionCategories', {
				headers: {
					Accept: 'application/prs.meiweixueyuan.v1+json'
				}
			})
			.then(res => {
				this.qaclassList = res.data.data;
				//console.log(this.qaclassList);
			})
			.catch(error => {
				console.log(error);
			});
			
			
			this.getQueData();
	},
	watch: {
		'$route': 'getQueData'
	},
	
};
</script>

<style></style>



