<template>
	<header class="header">
		<div class="header_wrap">
			<ul class="clearfix">
				<li class="logo">
					<h1>
						<router-link :to="{path:'/'}"></router-link>
					</h1>
				</li>
				<li class="search clearfix">
					<span></span>
					<form action="" method="get">
						<input id="inputText" type="text" name="search" value="" v-model="value" placeholder="搜索你想学的项目/产品">
					</form>
					<i @click="foucs"></i>
				</li>
				<li class="menu" @click="showSide">
					<i></i>
				</li>
			</ul>
		</div>
		<div class="mw-sub-nav mw-sub-nav-on" v-show="showhide">
			<div class="mw-nav-mask" @click="closeNav"><span class="iconfont icon-close"></span></div>
			<div class="mw-nav-con">
				<div class="c">
					<h2>美味学院</h2>
					<p>专注特色美食短期培训</p>
					<router-link :to="{path:'/'}"><i class="icon-home"></i>网站首页</router-link>
					<router-link :to="{path:'/courses'}"><i class="icon-project"></i>培训课程</router-link>
					<router-link :to="{path:'/articles'}"><i class="icon-zixun"></i>创业资讯</router-link>
					<router-link :to="{path:'/questions'}"><i class="icon-dayi"></i>创业答疑</router-link>
				</div>
			</div>
		</div>
	</header>
</template>

<script>

export default {
	name: 'headers',
	components:{
		
	},
	data() {
		return {
			value: '',
			showhide: false,
		}
	},
	methods: {
		foucs() {
			this.value = ''
		},
		showSide(){
		// this.$store.dispatch('showSideBar')
		this.showhide = true
		},
		closeNav(){
			this.showhide = false
		}
	},
	watch:{
	　　　'$route'(to,from){
				this.showhide = false
			}
	
	　　},
	
};
</script>

<style></style>



