<template>
	<div class="home">
		<div class="top_nav">
			<div class="top_nav_wrap">
				<template v-for="(item,index) in navList">
					<router-link :to="{path: 'courses',query:{category_id: item.id }}" :key="index">{{ item.name }}</router-link>
				</template>
			</div>
		</div>
		<div class="slider_banner">
			<template>
				<swiper :options="swiperOption" ref="mySwiper">
					<template v-for="banner in bannerList">
						<swiper-slide>
							<a :href="banner.url"><img :src="'https://img.xhmwxy.com/' + banner.image" :title="banner.title" :alt="banner.name" /></a>
						</swiper-slide>
					</template>
					<div class="swiper-pagination" slot="pagination"></div>
				</swiper>
			</template>
		</div>
		<div class="h_column">
			<ul>
				<li v-for="(column, index) in columnList" :key="index">
					<router-link :to="column.url">
						<div class="column_item"><i :style="{ backgroundImage: 'url(' + column.images + ')' }"></i></div>
						<p>{{ column.name }}</p>
					</router-link>
				</li>
			</ul>
		</div>
		<div class="prospect clearfix">
			<div class="prospect_left">
				<div class="prospect_tit clearfix">
					<p>创业当老板</p>
					<span>HOT</span>
				</div>
				<p>工作自由收入可观</p>
				<a :href="this.$counsel.shangWuTong">了解更多></a>
			</div>
			<div class="prospect_right">
				<div class="pr_who">
					<div class="prospect_tit clearfix"><p>创业当老板</p></div>
					<p>热门行业工资较高</p>
					<a :href="this.$counsel.shangWuTong">了解更多></a>
				</div>
				<div class="pr_who">
					<div class="prospect_tit clearfix"><p>创业当老板</p></div>
					<p>精彩生活有品位</p>
					<a :href="this.$counsel.shangWuTong">了解更多></a>
				</div>
			</div>
		</div>
		<!-- 单品及分类组件 -->
		<Product />
		<!-- 资讯分类 -->
		<div class="news">
			<ul class="news_tabs" id="newstabs">
				<li v-for="(tab,index) in tabList" :class="{ active: index == num }" @click="turnTab(index)" :key="index">{{ tab.name }}</li>
			</ul>
			<ul class="news_list" id="newslist">
				<li class="active" v-for="article in newsList">
					<!-- v-show="index == num" -->
					<router-link :to="{ path:'articles/'+article.id }" class="clearfix">
						<p>{{ article.name }}</p>
						<span>{{ article.created_time_date }}</span>
					</router-link>
				</li>
			</ul>
			<div class="more_btn"><router-link :to="{ path: '/articles' }">更多创业资讯>></router-link></div>
		</div>
		<!-- 创业答疑 -->
		<div class="qanda">
			<div class="class_tit clearfix">
				<p>创业答疑</p>
				<router-link :to="{path: 'question'}">
					您身边的创业指导专家
					<i></i>
				</router-link>
			</div>
			<div class="qa_item" v-for="que in queastons">
				<router-link :to="{path: 'questions/' + que.id }">
				<div class="question clearfix">
					<span class="qa_icon">Q</span>
					<p>{{ que.question }}</p>
				</div>
				<div class="answer clearfix">
					<span class="qa_icon">A</span>
					<!-- {{ que.answers.data }} -->
					<p><template v-for="ans in que.answers.data">{{ans.answer}}</template></p>
				</div>
				</router-link>
			</div>
			<div class="more_btn"><router-link :to="{ path: '/questions' }">更多创业问答>></router-link></div>
		</div>
	</div>
</template>

<script>
import Product from './soncomponents/product';
export default {
	name: 'home',
	components: {
		Product
	},
	created() {
		//头部列表
		this.$axios
			.get(this.HOST + '/wap/index/top', {
				headers: {
					Accept: 'application/prs.meiweixueyuan.v1+json'
				}
			})
			.then(res => {
				this.navList = res.data.categories;
				//console.log(this.navList);
			})
			.catch(error => {
				console.log(error);
			});
		//banner列表
		this.$axios
			.get(this.HOST + '/wap/index/banner', {
				headers: {
					Accept: 'application/prs.meiweixueyuan.v1+json'
				}
			})
			.then(res => {
				this.bannerList = res.data.data;
			})
			.catch(error => {
				console.log(error);
			});		
		//资讯分类
		this.$axios
			.get(this.HOST + 'articleCategories', {
				headers: {
					Accept: 'application/prs.meiweixueyuan.v1+json'
				}
			})
			.then(res => {
				this.tabList = res.data.data;
				//console.log(this.tabList)
			})
			.catch(error => {
				console.log(error);
			});
			//资讯默认显示
			this.$axios
				.get(this.HOST + 'articleCategories/1?include=articles', {
					headers: {
						Accept: 'application/prs.meiweixueyuan.v1+json'
					}
				})
				.then(res => {
					this.newsList = res.data.articles.data;
					//console.log(this.newsList)
				})
				.catch(error => {
					console.log(error);
				});
		//问答列表
		this.$axios
			.get(this.HOST + 'wap/index/questions?include=answers', {
				headers: {
					Accept: 'application/prs.meiweixueyuan.v1+json'
				}
			})
			.then(res => {
				this.queastons = res.data.data;
			})
			.catch(error => {
				console.log(error);
			});
	},
	methods: {
		turnTab(index) {
			this.num = index;
			index = index + 1;
			console.log(index)
			//资讯
			this.$axios
				.get(this.HOST + 'articleCategories/'+index+'?include=articles', {
					headers: {
						Accept: 'application/prs.meiweixueyuan.v1+json'
					}
				})
				.then(res => {
					this.newsList = res.data.articles.data;
					console.log(this.newsList)
				})
				.catch(error => {
					console.log(error);
				});
		}
	},
	data() {
		return {
			queastons: [],
			//answerlist: [],
			tabList: [],
			newsList: [],
			num: 0,
			columnList: [
				{
					name: '创业课程',
					url: 'courses',
					images: require('../assets/images/home_icon.png')
				},
				{
					name: '创业资讯',
					url: 'articles',
					images: require('../assets/images/home_icon.png')
				},
				{
					name: '美食百科',
					url: 'baike',
					images: require('../assets/images/home_icon.png')
				},
				{
					name: '创业问答',
					url: 'questions',
					images: require('../assets/images/home_icon.png')
				}
			],
			bannerList: [],
			swiperOption: {
				autoplay: true,
				pagination: {
					el: '.swiper-pagination'
				},
				loop: true,
				autoplay: true
			},
			navList: []
		};
	}
};
</script>

<style></style>



