<template>
	<div class="product_show_cont">
		<div class="psc_title">
			<h2>热门项目推荐</h2>
			<div class="title_line">
				<i></i>
				<i></i>
				<i></i>
			</div>
		</div>
		<div class="pro_recommend">
			<template v-for="hot in hotList">
				<div class="recommend_item">
					<router-link :to="{path: '/courses/'+hot.id+'/'+hot.slug}">
						<div class="recommend_pic"><img :src="hot.qiniu_url" alt="" /></div>
						<p>{{ hot.name }}</p>
						<p>报名人数：<span>{{ hot.apply_count }}</span></p>
					</router-link>
				</div>
			</template>
		</div>
	</div>
</template>

<script>
	export default{
		name: 'hotcommend',
		data() {
			return {
				hotList: []
			}
		},
		created() {
			this.$axios.get(this.HOST + 'course/recommends?take=3',{
				headers: {
					Accept: 'application/prs.meiweixueyuan.v1+json'
				}
			})
			.then( res => {
				this.hotList = res.data.data;
			//console.log(this.hotList)
			})
		}
	}
</script>

<style></style>



