import Vue from 'vue'
import mwxy from './App'
import Router from 'vue-router'
import routes from './config/routes'
Vue.use(Router)
//element-ui
import {Pagination} from 'element-ui'
//import 'element-ui/lib/theme-chalk/index.css'
Vue.prototype.$ELEMENT = { size: 'small', zIndex: 3000 }
Vue.use(Pagination)
//Vuex-store
import store from './store'
//swiper
import VueAwesomeSwiper from 'vue-awesome-swiper'
import 'swiper/dist/css/swiper.css'
Vue.use(VueAwesomeSwiper)
import counsel from './config/counsel'
Vue.prototype.$counsel = counsel
import Axios from 'axios'
var axios = Axios.create()
//axios.defaults.baseURL = ''    //apiConfig.baseUrl
axios.defaults.withCredentials = true
axios.defaults.responseType = 'json'
axios.defaults.transformResponse = [(data) => {
  return DataHandle.filterNull(data)
}] 

axios.interceptors.response.use(function (response) { // 请求成功的回调
  return Promise.resolve(response.data)
}, function (error) { // 请求失败的回调

  return Promise.reject(error)
})
Vue.prototype.$axios = Axios
//跨域 原url改为prototype.HOST //服务端做了nginx代理 这里我们就不跨域了
Vue.prototype.HOST = 'https://api.meiweixueyuan.cn/'
Vue.config.productionTip = false
const router = new Router({
	mode: 'history',
	base: __dirname,
	routes
})

/* eslint-disable no-new */
new Vue({
	el: '#mwxy',
	router,
	store,
	components: { mwxy },
	template: '<mwxy/>'
})



