<template>
	<div id="article">
		<div class="request_loading" v-if="loading">
			<div class="this_loading"></div>
		</div>
		<div class="current">
			<span>当前位置：</span><router-link :to="{path: '/'}">首页</router-link>><router-link :to="{path: 'articles'}">创业资讯</router-link>
		</div>
			<div class="tabs">
				<ul>
					<li v-for="(artclass,index) in articleClass" :key="index" @click="articleTab(index)" :class="{ active: index == nowChose }">
						<router-link :to="{ path:'articles',query:{category_id:artclass.id} }">{{ artclass.name }}</router-link>
					</li>
				</ul>
			</div>
			<div class="new_list">
				<div class="new_item clearfix" v-for="(art,index) in articleList" :key="index">
					<router-link :to="{ path: 'articles/'+ art.id }">
						<div class="new_pic">
							<img :src="art.qiniu_url" :alt="art.title" :title="art.title" />
						</div>
						<div class="new_brief">
							<h2>{{ art.name }}</h2>
							<p>{{ art.intro }}</p>
							<div class="new_icon clearfix">
								<i class="clock"></i>
								<p>{{ art.created_time_date }}</p>
								<i class="eye"></i>
								<p>{{ art.view_count }}</p>
							</div>
						</div>
					</router-link>
				</div>
			</div>
			<div class="turn_pages">
				<div class="turn_pages_wrap">
					<el-pagination
						@current-change="handleCurrentChange"
						:current-page.sync="thisArtPage"
						prev-text="上一页"
						background
						layout="prev, pager, next"
						:page-count="totalArtPage"
						:pager-count="5"
						hide-on-single-page
						next-text="下一页">
					</el-pagination>
					<span>共{{ totalArtPage }}页</span>
				</div>
			</div>
	</div>
</template>

<script>
export default {
	name: 'articles',
	data() {
		return {
			articleList:[],
			articleClass: {},
			nowChose: 0,
			thisArtPage: 1,
			totalArtPage: 1,
			loading: false
		}
	},
	created() {
		//分类
		this.$axios.get(this.HOST + 'articleCategories',{
			headers:{
				Accept: 'application/prs.meiweixueyuan.v1+json'
			}
		})
		.then( res=> {
			this.articleClass = res.data.data;
		})
		.catch( error => {
			console.log(error)
		});
		this.getArticleData();
	},
	methods:{
		articleTab(index){
			this.nowChose = index;
			this.thisArtPage = 1;
		},
		getArticleData(){
			this.loading = true;
			this.nowChose = this.$route.query.category_id - 1
			var artCateId = this.$route.query.category_id;
			if(!this.$route.query.category_id){
				artCateId = 1;
				this.thisArtPage = 1;
				this.nowChose = 0;
			}
			this.$axios.get(this.HOST + '/articles?category_id=' + artCateId + '&page=' + this.thisArtPage,{
				headers:{
					Accept: 'application/prs.meiweixueyuan.v1+json'
				}
			})
			.then( res => {
				this.loading = false;
				var articleAllList = res.data;
				this.thisArtPage = res.data.meta.pagination.current_page;
				this.totalArtPage = res.data.meta.pagination.total_pages;
				this.articleList = articleAllList.data
				return this.articleList.reverse();
				
			})
			.catch(error =>{
				console.log(error)
			});
		},
		handleCurrentChange(val){
			if(this.$route.query.category_id){
				var route =  '/articles?category_id=' + this.$route.query.category_id + '&page=' + val;
				this.$router.replace(route);
				this.thisArtPage = val;
				
			}else{
				var route = '/articles?page=' + val;
				this.$router.replace(route);
				this.thisArtPage = val;
				this.nowChose = 0
			}
				  
		},
	},
	watch: {
		'$route': 'getArticleData'
	},
};
</script>

<style></style>



