			</div>
		</div>
		<div class="product_show">
			<div class="show_box">
				<div class="pro_show_pic"><img :src="coursesDelite.qiniu_url" :alt="coursesDelite.title" /></div>
				<div class="register clearfix">
					<div class="register_number">
						<div class="number_yh">
							报名优惠：
							<span>学二送二 多学多送</span>
						</div>
						<div class="number_hb clearfix">
							<p>市场火爆指数：</p>
							<span v-for="start in coursesDelite.exponent">{{ start }}</span>
						</div>
					</div>
					<div class="register_btn clearfix">
