						<a :href="this.$counsel.shangWuTong">立即咨询</a>
					</div>
				</div>
				<div class="register_show">
					<div class="reg_show_left clearfix">
						<i></i>
						<p>咨询人数：{{ coursesDelite.consult_count }}</p>
					</div>
					<div class="reg_show_right clearfix">
						<i></i>
						<p>报名人数：{{ coursesDelite.apply_count }}</p>
					</div>
				</div>
			</div>
		</div>

		<div class="product_show_cont">
			<div class="psc_title">
				<h2>项目介绍</h2>
				<div class="title_line">
					<i></i>
					<i></i>
					<i></i>
				</div>
			</div>
			<div class="psc_word" v-html="coursesDelite.body">{{ coursesDelite.body }}</div>
			<div class="psc_btn">
				<a :href="this.$counsel.shangWuTong">我想咨询</a>
				<a :href="this.$counsel.shangWuTong">我想学习</a>
			</div>
		</div>

