					<i></i>
					<i></i>
				</div>
			</div>
			<div class="learn_what">
				<div class="what_title addmargin clearfix">
					<span>A</span>
					<p>理论学习：</p>
				</div>
				<p>1、流程讲解，包括的选址、人员配备、物资采购等。</p>
				<p>2、项目技术详细批解，包括原材料的选择，采购，口味的变换、配比等。</p>
				<p>3、经营小技巧，轻松创造。</p>
				<div class="what_title clearfix">
