					<p>示范讲解：</p>
				</div>
				<p>1、专业老师一对一的实战示范，讲解技术。</p>
				<p>2、老师实践操作中带领你回忆理论学习阶段中的开店的技巧，原材料的选择，采购，成本、预算。</p>
				<div class="what_title clearfix">
					<span>C</span>
					<p>自我实践：</p>
				</div>
				<p>1、自己操作所有流程，制作出成品，老师旁边检查指导。</p>
				<p>2、学员自我思索，老师引导，熟透操作流程。</p>
			</div>
			<div class="psc_btn">
				<a :href="this.$counsel.shangWuTong">我想咨询</a>
				<a :href="this.$counsel.shangWuTong">我想学习</a>
			</div>
		</div>
		<div class="product_show_cont">
			<div class="psc_title">
				<h2>为什么选我们</h2>
				<div class="title_line">
					<i></i>
					<i></i>
					<i></i>
				</div>
			</div>
			<div class="superiority">
				<ul>
					<li>
						<div class="sup_icon"></div>
						<h3>品牌保障</h3>
						<p>30多年集团办学经验<br />全国连锁品牌</p>
					</li>
					<li>
						<div class="sup_icon"></div>
						<h3>实操实训</h3>
						<p>全程实操<br />理论融入实践</p>
					</li>
					<li>
						<div class="sup_icon"></div>
						<h3>教学特色</h3>
						<p>上课时间灵活 小班<br />教学 手把手指导</p>
					</li>
					<li>
						<div class="sup_icon"></div>
						<h3>师资雄厚</h3>
						<p>精技术 擅经营 重创<br />新 专业教师队伍</p>
					</li>
