						<div class="sup_icon"></div>
						<h3>市场接轨</h3>
						<p>紧随时尚潮流<br />产品更新换较快</p>
					</li>
					<li>
						<div class="sup_icon"></div>
						<h3>开店指导</h3>
						<p>免费开店技术指导和<br />成本核算知识</p>
					</li>
				</ul>
			</div>
			<div class="psc_btn">
				<a :href="this.$counsel.shangWuTong">我想咨询</a>
				<a :href="this.$counsel.shangWuTong">我想学习</a>
			</div>
		</div>
		<div class="product_show_cont">
			<div class="psc_title">
				<h2>培训实景</h2>
				<div class="title_line">
					<i></i>
					<i></i>
					<i></i>
				</div>
			</div>
			<div class="scene_pic"><img :src="require('../../assets/images/real_scene.jpg')" alt="培训实景图" /></div>
