				<a :href="this.$counsel.shangWuTong">我想咨询</a>
				<a :href="this.$counsel.shangWuTong">我想学习</a>
			</div>
		</div>
		<Hot />
		<Tables />
	</div>
	</transition>
</template>

<script>
import Tables from '../../layouts/table';
import Hot from '../soncomponents/hotrecommend.vue';
export default {
	name: 'courseShow',
	components: {
		Tables,
		Hot
	},
	
	data() {
		return {
			coursesDelite: [],
			routerAlive:true,
		};
	},

	created() {
		var reg = /\/show\/(\d+)/;
		//var id = this.$route.path.match(reg);
		//console.log(this.$route.path.match(reg)[1])
		var id = this.$route.params.id;
		//console.log(id);
		this.getData(id);
	},
	methods: {
		getData(id) {
			this.$axios
				.get(this.HOST + '/courses/'+id, {
					headers: {
						Accept: 'application/prs.meiweixueyuan.v1+json'
					}
				})
				.then((res) => {
					this.coursesDelite = res.data;
				})
				.catch(error => {
					console.log(error);
				});
		}
	},
	watch:{
	　　　'$route'(to,from){//监听路由发生变化时，组件重新渲染，页面刷新
	　　　　　　this.isRouterActive = false;
				window.location.reload()
			}
	　　},
}
</script>

<style></style>



