<template>
  <div class="project_class">
    <div class="pro_wrap">
      <div class="class_tit clearfix">
        <p>热门火爆培训</p>
        <router-link :to="{path:'courses'}">
          四季经营 投资小 收益可观
          <i></i>
        </router-link>
      </div>
    </div>
    <div class="class_item">
      <div class="pro_wrap clearfix">
        <div class="class_lader">
          <div class="lader_icon icon_01">
            <i></i>
          </div>
          <p>特色小吃</p>
        </div>
        <div class="class_content">
          <ul class="clearfix">
            <li v-for="(pro1,index) in proList1" v-if="index<13">
              <router-link
                :to="{ path: 'courses/' + pro1.id + '/' + pro1.slug }"
                :class="{'active': pro1.hotted}">{{ pro1.name }}{{ pro1.id }}</router-link>
            </li>
            <li>
              <router-link :to="{path:'courses'}">更多项目+</router-link>
            </li>
          </ul>
        </div>
      </div>
    </div>
    <div class="class_item">
      <div class="pro_wrap clearfix">
        <div class="class_lader">
          <div class="lader_icon icon_02">
            <i></i>
          </div>
          <p>早点面点</p>
        </div>
        <div class="class_content">
          <ul class="clearfix">
            <li v-for="(pro2,index) in proList2" v-if="index < 15">
              <router-link
                :to="{ path: 'courses/' + pro2.id + '/' + pro2.slug }"
                :class="{'active': pro2.hotted}">{{ pro2.name }}</router-link>
            </li>

            <li>
              <router-link :to="{path:'courses'}">更多项目+</router-link>
            </li>
          </ul>
        </div>
      </div>
    </div>
    <div class="class_item">
      <div class="pro_wrap clearfix">
        <div class="class_lader">
          <div class="lader_icon icon_03">
            <i></i>
          </div>
          <p>烧烤卤菜</p>
        </div>
        <div class="class_content">
          <ul class="clearfix">
            <li v-for="(pro3,index) in proList3" v-if="index < 15">
              <router-link
                :to="{ path: 'courses/' + pro3.id + '/' + pro3.slug }"
                :class="{'active': pro3.hotted}">{{ pro3.name }}</router-link>
            </li>
            <li>
             <router-link :to="{path:'courses'}">更多项目+</router-link>
            </li>
          </ul>
        </div>
      </div>
    </div>
    <div class="class_item">
      <div class="pro_wrap clearfix">
        <div class="class_lader">
          <div class="lader_icon icon_04">
            <i></i>
          </div>
          <p>西点蛋糕</p>
        </div>
        <div class="class_content">
          <ul class="clearfix">
            <li v-for="(pro4,index) in proList4" v-if="index < 15">
              <router-link
                :to="{ path: 'courses/' + pro4.id + '/' + pro4.slug }"
                :class="{'active': pro4.hotted}">{{ pro4.name }}</router-link>
            </li>
            <li>
              <router-link :to="{path:'courses'}">更多项目+</router-link>
            </li>
          </ul>
        </div>
      </div>
    </div>
    <div class="class_item">
      <div class="pro_wrap clearfix">
        <div class="class_lader">
          <div class="lader_icon icon_05">
            <i></i>
          </div>
          <p>饮品咖啡</p>
        </div>
        <div class="class_content">
          <ul class="clearfix">
            <li v-for="(pro5,index) in proList5" v-if="index < 15">
              <router-link
                :to="{ path: 'courses/' + pro5.id + '/' + pro5.slug }"
                :class="{'active': pro5.hotted}">{{ pro5.name }}</router-link>
            </li>

            <li>
              <router-link :to="{path:'courses'}">更多项目+</router-link>
            </li>
          </ul>
        </div>
      </div>
    </div>
  </div>
</template>
<script>
export default {
  name: "product",
  data() {
    return {
      proList1: [],
      proList2: [],
      proList3: [],
      proList4: [],
      proList5: [],
      courses_id:[9,10,15,11,13],
    }
  },
  created() {
    //1特色小吃
    this.$axios
      .get(this.HOST + "/recommends/9?include=courses", {
        headers: {
          Accept: "application/prs.meiweixueyuan.v1+json"
        }
      })
      .then(res => {
        this.proList1 = res.data.courses.data;
      })
      .catch(error => {
        console.log(error);
      });

      //2早点面点
      this.$axios
      .get(this.HOST + "/recommends/10?include=courses", {
        headers: {
          Accept: "application/prs.meiweixueyuan.v1+json"
        }
      })
      .then(res => {
        this.proList2 = res.data.courses.data;
      })
      .catch(error => {
        console.log(error);
      });

      //3烧烤卤菜
      this.$axios
      .get(this.HOST + "/recommends/15?include=courses", {
        headers: {
          Accept: "application/prs.meiweixueyuan.v1+json"
        }
      })
      .then(res => {
        this.proList3 = res.data.courses.data;
      })
      .catch(error => {
        console.log(error);
      });

      //4西点蛋糕
      this.$axios
      .get(this.HOST + "/recommends/11?include=courses", {
        headers: {
          Accept: "application/prs.meiweixueyuan.v1+json"
        }
      })
      .then(res => {
        this.proList4 = res.data.courses.data;
      })
      .catch(error => {
        console.log(error);
      });
      //5饮品咖啡
      this.$axios
      .get(this.HOST + "/recommends/13?include=courses", {
        headers: {
          Accept: "application/prs.meiweixueyuan.v1+json"
        }
      })
      .then(res => {
        this.proList5 = res.data.courses.data;
      })
      .catch(error => {
        console.log(error);
      });
  }
};
</script>


