import Home from '@/components/home'
import Courses from '@/components/courses/courses'
import courseShow from '@/components/courses/show'
import Question from "@/components/questions/questions"
import questionShow from "@/components/questions/show"
import Articles from "@/components/articles/articles"
import articleShow from "@/components/articles/show"
import NotFound from "@/NotFound/NotFound"
import Baike from '@/components/baike/baike'

export default [{
		path: '/',
		name: 'home',
		component: Home,
	},
	{
		path: '*',
		name: 'NotFound',
		component: NotFound,
		meta:{
			title: '你要的东西找不到'
		}
	},
	{
		path: '/baike',
		name: 'baike',
		component: resolve => require(['@/components/baike/baike'], resolve),
	},
	{
		path: '/courses',
		name: 'courses',
		component: resolve => require(['@/components/courses/courses'], resolve),
		//redirect: 'courses'
	},
	{
		path: '/courses/:id/:slug',
		name: 'courseShow',
		component: resolve => require(['@/components/courses/show'], resolve),
		alias: '/courses/show/:id/',
		// redirect: '/courses/:id/:slug'
	},
	{
		path: '/questions',
		name: 'questions',
		component: resolve => require(['@/components/questions/questions'], resolve),
	},
	{
		path: '/questions/:id',
		name: 'questionShow',
		component: resolve => require(['@/components/questions/show'], resolve),
		alias: '/questions/show/:id'
	},
	{
		path: '/articles',
		name: 'articles',
		component: resolve => require(['@/components/articles/articles'], resolve),
	},
	{
		path: '/articles/:id',
		name: 'articleSshow',
		component: resolve => require(['@/components/articles/show'], resolve),
		alias: '/articles/show/:id'
	},

]


