<template>
  <div id="mwxy">
		<Header />
		<keep-alive exclude="courseShow,articleShow,questionShow">
		<router-view />
		</keep-alive>
		<Footer />
  </div>
</template>

<script>
import Header from './layouts/header'
import Footer from './layouts/footer'
export default {
	name: 'mwxy',
	components:{
		Header,
		Footer,
	},
	methods: {
		
	},
	watch:{
		scrollBehavior (to, from) {
			return { x: 0, y: 0 }
		}
	}
}
</script>

<style lang="less">
	#mwxy{
		padding-top: .85rem;
		}
	@import "./style/style";
</style>




