<template>
	<div id="courseShow">
		<div class="request_loading" v-if="loading">
			<div class="this_loading"></div>
		</div>
		<div class="product_nav">
			<ul>
				<li v-for="(item, index) in navList" :key="index" :class="{ pro_nav_active: index == num }" @click="coursesTab(index)">
					<router-link :to="{ path: 'courses', query: { category_id: item.id } }">{{ item.name }}</router-link>
				</li>
			</ul>
		</div>
		<div class="product_list">
			<ul>
				<li v-for="pro in proList">
					<router-link :to="{ path: 'courses/' + pro.id + '/' + pro.slug }">
						<div class="product_pic"><img :src="'https://img.xhmwxy.com/' + pro.image" :alt="pro.name" /></div>
						<div class="product_presentation clearfix">
							<div class="pro_pre_word">
								<span>{{ pro.name }}</span>
								<p>
									报名人数：
									<span>{{ pro.apply_count }}</span>
								</p>
							</div>
							<div class="pro_pre_icon"><i></i></div>
						</div>
					</router-link>
				</li>
			</ul>
		</div>
		<div class="turn_pages">
			<div class="turn_pages_wrap">
				<el-pagination
					@current-change="handleCurrentChange"
					:current-page="thisCourPage"
					prev-text="上一页"
					background
					layout="prev, pager, next"
					:page-count="courTotalPage"
					:pager-count="5"
					hide-on-single-page
					next-text="下一页">
				</el-pagination>
				<span>共{{ courTotalPage }}页</span>
			</div>
		</div>
	</div>
</template>

<script>
export default {
	name: 'courseList',
	data() {
		return {
			navList: [],
			num: 0,
			proList: [],
			pages: [],
			courTotalPage: 1,
			thisCourPage: 1,
			loading: false
		};
	},
	created() {
		this.$axios
			.get(this.HOST + '/wap/index/top', {
				headers: {
					Accept: 'application/prs.meiweixueyuan.v1+json'
				}
			})
			.then(res => {
				this.navList = res.data.categories;
				for (let i = 0; i < this.navList.length; i++) {
					//console.log(this.navList[i].id)
					this.theId = this.navList[i].id;
				}
				//console.log(this.navList);
			})
			.catch(error => {
				console.log(error);
			});
		this.getCourData();
	},
	methods: {
		coursesTab(index){
			this.num = index;
			this.thisCourPage = 1;
		},
		getCourData() {
			this.loading = true;
			this.num = this.$route.query.category_id - 1;
			var curCateId = 'category_id=' + this.$route.query.category_id;
			if(!this.$route.query.category_id){
				curCateId = '';
				this.thisCourPage + 1;
				this.num = null;
			}
			this.$axios
				.get(this.HOST + '/courses?' + curCateId + '&page=' + this.thisCourPage , {
					headers: {
						Accept: 'application/prs.meiweixueyuan.v1+json'
					}
				})
				.then(res => {
					this.loading = false;
					this.proList = res.data.data;
					this.pages = res.data.meta.pagination;
					this.courTotalPage = this.pages.total_pages;
					return this.proList.reverse();
				})
				.catch(error => {
					console.log(error);
				});
		},
		handleCurrentChange(val){
			if(this.$route.query.category_id){
				var route =  '/courses?category_id=' + this.$route.query.category_id + '&page=' + val;
				this.$router.replace(route);
				this.thisCourPage = val;
				
			}else{
				var route = '/courses?page=' + val;
				this.$router.replace(route);
				this.thisCourPage = val;
				this.num = 0
			}
				  
		},
	},
	watch: {
		'$route': 'getCourData'
	}
};
</script>

<style></style>



